*,
*::before,
*::after {
  box-sizing: inherit;
}

* {
  margin: 0;
}

html {
  font-size: 62.5%;
  box-sizing: border-box;
}

html:focus-within {
  scroll-behavior: smooth;
}

html,
body {
  height: 100%;
}

body {
  color: #999;
  font-family: "Roboto", sans-serif;
  font-size: 1.6rem;
  font-weight: 400;
  line-height: 1.5;
}

ul,
ol {
  padding: 0;
  list-style: none;
}

a {
  color: inherit;
  text-decoration: none;
}

img,
picture,
svg {
  display: block;
  max-width: 100%;
  height: auto;
}

input,
button {
  font: inherit;
}

[role=button] {
  cursor: pointer;
}

.heading-tertiary, .heading-secondary, .heading-primary {
  font-family: "Playfair Display", serif;
  line-height: 1;
  letter-spacing: -0.7px;
  margin-bottom: 1rem;
}

.heading-primary {
  font-size: 3.2rem;
}

.heading-secondary {
  font-size: 2.4rem;
}

.heading-tertiary {
  font-size: 1.8rem;
}

.btn, .btn:link, .btn:visited {
  border: none;
  border-radius: 1rem;
  cursor: pointer;
  font-family: inherit;
  text-align: center;
  display: inline-block;
  transition: all 0.4s ease-in-out;
}

.btn--primary {
  font-size: 1.8rem;
  background-color: #610972;
  color: #999;
}
.btn--primary:hover {
  background-color: #390543;
}
.btn--secondary {
  font-size: 1.4rem;
  background-color: #ecb009;
  color: #333;
}
.btn--secondary:hover {
  background-color: #bb8b07;
}

.container {
  max-width: 120rem;
  width: 100%;
  margin: 0 auto;
}

.grid-responsive {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(24rem, 1fr));
  gap: 2rem;
}

.flex-column {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  gap: 1.6rem;
}