@use 'base/variables' as v;
@use 'base/mixins' as m;

*,
*::before,
*::after {
  box-sizing: inherit;
}

* {
  margin: 0;
}

html {
  font-size: v.$font-size-base;
  box-sizing: border-box;
}

// Aplica rolagem suave aos elementos que receberem o foco
html:focus-within {
  scroll-behavior: smooth;
}

html,
body {
  height: 100%;
}

body {
  color: v.$color-grey-dark-2;
  font-family: v.$font-family-body;
  font-size: v.$font-size-body;
  font-weight: v.$font-weight-normal;
  line-height: v.$line-height-base;
}

ul,
ol {
  padding: 0;
  list-style: none;
}

a {
  color: inherit;
  text-decoration: none;
}

img,
picture,
svg {
  display: block;
  max-width: 100%;
  height: auto;
}

input,
button {
  font: inherit;
}

[role='button'] {
  cursor: pointer;
}
